/* Write your CSS here */
.body_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    align-self: stretch;
  }
  
  @media screen and (min-width: 768px) {
    .body_container {
      flex-direction: row;
      justify-content: space-between;
      height: 448px;
      margin-top: 8px;
      margin-bottom: 12px;
    }
  }
  
  .left_Navbar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-self: stretch;
    flex-wrap: wrap;
    flex-shrink: 0;
    color: #475569;
    background-color: #e2e8f0;
    height: 30%;
    padding: 32px;
    margin-top: 2px;
    margin-bottom: 2px;
  }
  
  @media screen and (min-width: 768px) {
    .left_Navbar {
      justify-content: flex-start;
      align-items: flex-start;
      height: 100%;
      width: 30%;
      max-width: 250px;
      margin-right: 2px;
      margin-left: 2px;
    }
  }

  .body_heading {
    color: #475569;
    text-align: center;
    font-family: 'Roboto';
    font-size: 18px;
    font-weight: 600;
  }
  @media screen and (min-width: 768px) {
    .body_heading {
      text-align: left;
      font-size: 20px;
    }
  }
  
  .list_items {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    list-style-type: none;
    margin: 0px;
    padding: 0px;
  }
  
  @media screen and (min-width: 768px) {
    .list_items {
      flex-direction: column;
    }
  }
  
  .item {
    font-family: 'Roboto';
    font-size: 20px;
    margin: 10px;
  }
  
  @media screen and (min-width: 768px) {
    .item {
      font-size: 24px;
    }
  }
  
  .content_container {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    background-color: #f1f5f9;
    height: 100%;
    padding: 32px;
    margin-top: 6px;
    margin-bottom: 2px;
  }
  
  @media screen and (min-width: 768px) {
    .content_container {
      margin-right: 2px;
      margin-left: 2px;
    }
  }
  
  .body_content {
    color: #475569;
    text-align: center;
    font-family: 'Roboto';
    font-size: 20px;
    margin: 0px;
  }
  
  @media screen and (min-width: 768px) {
    .body_content {
      text-align: left;
      font-size: 24px;
      margin: 0px;
    }
  }
  
  .right_Navbar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: stretch;
    flex-shrink: 0;
    background-color: #e2e8f0;
    height: 30%;
    padding: 32px;
    margin-top: 2px;
    margin-bottom: 2px;
  }
  
  @media screen and (min-width: 768px) {
    .right_Navbar {
      justify-content: flex-start;
      height: 100%;
      width: 30%;
      max-width: 250px;
      padding: 32px;
      margin-right: 2px;
      margin-left: 2px;
    }
  }
  
  .adBox_container {
    display: flex;
    justify-content: center;
  }
  
  @media screen and (min-width: 768px) {
    .adBox_container {
      flex-direction: column;
      width: 100%;
    }
  }
  
  .ad {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #475569;
    font-family: 'Roboto';
    font-size: 20px;
    height: 150px;
    width: 180px;
    border: 1px solid #64748b;
    margin: 5px;
  }
  
  @media screen and (min-width: 768px) {
    .ad {
      font-size: 24px;
      width: 100%;
      margin-bottom: 10px;
      margin-right: 0px;
    }
  }